<script>
// import Vue from 'vue';
import api from "@/services/http";
import Register from '@/components/Register';
export default {
  components: {
    Register,
  },

  data: () => ({
    alertpesan: true,
    value: 0,
    data:[],
    token:null,
    query: false,
    show: true,
    interval: 0,
    DialogKetikaLogin: false,
    DialogLoginMobile: false,
    DialogRegisterMobile: false,
    mobile:null,
    windowSize: {x: 0, y: 0},
    loading:false,
    TampilkanSandi: false,
    pesan:'',
    credentials:{
      Nama: '',
      password: ''
    },

    return:{
      loading: true
    },     
  }),

  mounted(){
    this.queryAndIndeterminate()
    // console.log(api.defaults.baseURL)
  },

  beforeDestroy () {
    clearInterval(this.interval)
  },

  watch: {
    windowSize(){
      if (this.windowSize.x < 500) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },
  },

  methods: {
    register() {
      this.DialogRegisterMobile = true
    },

    queryAndIndeterminate () {
      this.query = true
      this.show = true
      this.value = 0

      setTimeout(() => {
        this.query = false

        this.interval = setInterval(() => {
          if (this.value === 100) {
            clearInterval(this.interval)
            this.show = true
            return setTimeout(this.queryAndIndeterminate, 3000)
          }
          this.value += 25
        }, 500)
      }, 300)
    },

    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },

    checkFlag() {
      if(this.$menu == undefined) {
        var cek = window.setTimeout(this.checkFlag, 100); /* this checks the flag every 100 milliseconds*/
      } else {
        localStorage.setItem('Menu',JSON.stringify(this.$menu))
        this.$router.push('/')
        clearTimeout(cek)
      }
    },
    inputToken(d){
      // let data = JSON.parse(localStorage.getItem('user'))
      if(d == "Mobile"){
        //token mobile update
        console.log('mobile')

        api.put("/inputToken/"+this.data.user.id+'/?token='+this.token,{
            Tokenz:"Mobile",
            Value: this.token
					},
          // { headers: {"Authorization" : `Bearer ${this.token}` } }
          ).then((res) => {
						if(res){
              console.log("Token Mobile Saved")
              this.Toast('Terhubung Dengan TSS')
              this.DialogKetikaLogin = true
              setTimeout(() => {
                this.DialogKetikaLogin = false
              }, 4000);
              localStorage.setItem('token', this.token)
              localStorage.setItem('user', JSON.stringify(this.data.user))
              //triger ke appbar via root dan emit
              this.checkFlag();
              this.$root.$emit('menuappbar')
              sessionStorage.setItem('greeting',0)
              }
					})
      }else{
        //token Desktop update
        console.log("Desktop")
        api.put("/inputToken/"+this.data.user.id+'/?token='+this.token,{
            Tokenz:"Desktop",
            Value: this.token
					},
          // { headers: {"Authorization" : `Bearer ${this.token}` } }
          ).then((res) => {
						if(res){
              console.log("Token Desktop Saved")
              this.Toast('Terhubung Dengan TSS')
              this.DialogKetikaLogin = true
              setTimeout(() => {
                this.DialogKetikaLogin = false
              }, 4000);
              localStorage.setItem('token', this.token)
              localStorage.setItem('user', JSON.stringify(this.data.user))
              //triger ke appbar via root dan emit
              this.checkFlag();
              this.$root.$emit('menuappbar')
              sessionStorage.setItem('greeting',0)
              }
					})
      }
    },

    Toast(data){
      this.$swal.fire({
      toast: true,
      icon: 'success',
      title: data,
      animation: false,
      showCloseButton: true,
      position: 'top-right',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true})
    },

    async login(){
    let device = ''
    let cek = navigator.userAgent.split(/\s*[;)(]\s*/);
    if(cek[1] == "Linux" || cek[1] == "iPhone" || cek[1] == "iPad"){
      device = "Mobile"
    }else{
      device = "Desktop"
    }
     this.pesan = ''
     this.alertpesan = true
      this.loading = true
      api.post('/login', this.credentials)
        .then(res=>{
          if (res.data.status == false) {
          this.loading = false
          this.pesan = "Nama Pengguna atau Kata Sandi Mungkin Salah!"
          }else if(res.data.status == "Akun Non Aktif"){
            this.loading = false
            this.pesan = "Akun Dinonaktifkan silahkan hubungi HR Atau Team IT Terima Kasih"
          }else {
            if(device == "Desktop"){
              if(res.data.user.TokenDesktop != null){
              this.Toast('Sudah Login Di Desktop')
              this.loading = false
            }else{
              this.token = res.data.token
              this.data = res.data
              this.inputToken(device)
              }
            }
            if(device == "Mobile"){
              if(res.data.user.TokenMobile != null){
              this.Toast('Sudah Login Di Mobile')
              this.loading = false
            }else{
              this.token = res.data.token
              this.data = res.data
              this.inputToken(device)
              // this.Toast('Terhubung Dengan TSS')
              // this.DialogKetikaLogin = true
              // setTimeout(() => {
              //   this.DialogKetikaLogin = false
              // }, 4000);
              // localStorage.setItem('token', res.data.token)
              // localStorage.setItem('user', JSON.stringify(res.data.user))
              // //triger ke appbar via root dan emit
              // this.checkFlag();
              // this.$root.$emit('menuappbar')
              // sessionStorage.setItem('greeting',0)
              }
            }
            
            
            //   const menu = async () => {
            //   const menulist = await this.$menu
            //   localStorage.setItem('Menu',JSON.stringify(menulist))
            //   console.log('ok :::',menulist);
            // }
              
              // while(this.$menu == undefined) {
              //   console.log('aww')
              //   }
            // const PromiseMenu = new Promise((resolve) => {
            //   setTimeout(resolve, 500, this.$menu);
            // });
            // var PromiseMenu = new Promise((resolve) => {
            //         resolve(this.$menu);
            //   });
            // Promise.all([PromiseMenu]).then((values) => {
            //   console.log('prm',values[0])
            //   // localStorage.setItem('Menu',JSON.stringify(values[0]))
            //   // this.$router.push('/')
            // });
            // var isfinished
            // var timeout = setInterval(function() {
            //   isfinished = false
            //       if(this.$menu.length > 0 ) {
            //           clearInterval(timeout); 
            //           localStorage.setItem('Menu',JSON.stringify(this.$menu))
            //           isfinished = true;
            //       }
            //   }, 100);
            // console.log(res)
          }
        })
        .catch(err=>{
          if (err == "Error: Request failed with status code 500") {
            this.loading = false
            alert('koneksi database bermasalah')
          }
          // alert('email atau password salah')
          // confirm('email atau password salah')
          console.log('email atau password salah')
        })
    },
  },
};

</script>

